@font-face{font-family:Leafy;src:url(./leafy.woff)}.home .name-input{font-family:"Leafy";border:1px solid transparent;padding:0px 5px}.home .name-input:focus{outline:none;-webkit-box-shadow:3px 3px 7px 0px rgba(0,0,0,0.185);box-shadow:3px 3px 7px 0px rgba(0,0,0,0.185)}.home{text-align:center}.home .name-input{position:relative;border-radius:10px;-webkit-box-shadow:3px 3px 7px 0px rgba(0,0,0,0.185);box-shadow:3px 3px 7px 0px rgba(0,0,0,0.185);top:20vh;left:0;width:100%;font-size:20px;font-family:"Leafy";line-height:30px;padding-bottom:8px}.home .button-group{position:relative;top:40vh}.home .button-group .generic-button{margin:15px;border-radius:10px}.home .button-group .create-btn{-webkit-transition:300ms cubic-bezier(0.53, -0.49, 0.43, 1.62);-o-transition:300ms cubic-bezier(0.53, -0.49, 0.43, 1.62);transition:300ms cubic-bezier(0.53, -0.49, 0.43, 1.62)}.home .button-group .join-btn{-webkit-transition:300ms cubic-bezier(0.53, -0.49, 0.43, 1.62) 100ms;-o-transition:300ms cubic-bezier(0.53, -0.49, 0.43, 1.62) 100ms;transition:300ms cubic-bezier(0.53, -0.49, 0.43, 1.62) 100ms}.home .button-group .create-btn.invisible{opacity:0;bottom:-100px}.home .button-group .join-btn.invisible{opacity:0;bottom:-100px}.home .button-group .create-btn.visible{bottom:0px;opacity:1}.home .button-group .join-btn.visible{bottom:0px;opacity:1}



